<template>
  <!-- メッセージエリア -->
  <div id="global-message">
    <b-alert variant="danger" dismissible show v-show="messageError" class="mb-0">
      {{ messageError }}
    </b-alert>
    <b-alert
      variant="warning" dismissible show v-show="messageWarnings.length > 0" class="mb-0">
      <p v-for="warning in messageWarnings" class="mb-0">{{ warning }}</p>
    </b-alert>
    <b-alert variant="info" dismissible show v-show="messageInfo" class="mb-0">
      {{ messageInfo }}
    </b-alert>
  </div>
</template>

<script>
export default {
  name: 'GlobalMessage',
  computed: {
    messageError: function () {
      console.log('messageErrorです。')
      return this.$store.state.messageData.error
    },
    messageWarnings: function() {
      return this.$store.state.messageData.warnings
    },
    MessageInfo: function() {
      return this.$store.state.messageData.info
    }
  }
}
</script>



